@import url('https://fonts.googleapis.com/css?family=Montserrat:600');

html, body {
      font: 14px/1.21 'Helvetica Neue', arial, sans-serif;
      font-weight: 400;
      box-sizing: border-box;
    }

    *, *::before, *::after {
      box-sizing: inherit;
      margin: 0;
      padding: 0;
    }

    h1 {
      text-align: center;
      margin-top: 40px;
      margin-bottom: 40px;
    }

    .chat-window {
      width: 500px;
      margin: auto;
      border: 1px solid #d5d5d5;
      
      margin-bottom: 40px;


    }

    .conversation-view {
      width: 100%;
      height: 100%;
      padding: 20px 40px;
      
     

    
      
    }

    .message-box {
      width: 100%;
      height: 50%;
      padding: 20px 20px;
    }

    .text-input {
      width: 100%;
      border: none;
      padding: 5px;

    }

    .chat-bubble {
      font-size: 20px;
      margin-bottom: 20px;
      width: 100%;
      display: flex;
      
     
     
    }

    .chat-bubble.ai {
      justify-content: flex-end;

      
    }

    .chat-bubble.ai .chat-content {
      background-color: #2E86C1;
      border: 2px;
      border-color: #2E86C1;
      color: white !important;
      


      
    }




    .chat-content {
     
      background-color: white;
      color: black;
       font-family: 'Montserrat', sans-serif;
       font-size: 15px;
        box-shadow: 0 0 6px #B2B2B2;
        display: inline-block;
        padding: 10px 18px;
        position: relative;
        vertical-align: middle;
        border-radius: 10px;
        max-width: 80%;

    }

 .header {

  background-color: #34495E;
  color: white;

 }

.header-text {

  font-family: 'Montserrat', sans-serif;
  margin-top: 15px;
  margin-left: 25px;
}